<template>
  <div ref="guestspicker" class="guests-picker">
    <div class="picker-button" :class="{ 'active': isOpen, 'disabled': disabled }" @click="toggle">
      <i class="pi pi-users" />
      <span>{{ value.length }} {{ value.length > 1 ? 'Rooms/Sites' : 'Room/Site' }}, {{ total }} {{ total > 1 ? 'Guests'
      : 'Guest' }}</span>
    </div>
    <div v-if="isOpen" class="picker-modal">
      <i class="close pi pi-times" @click="toggle" />
      <div class="picker-header">
        <span />
        <div>
          <h4>Adults</h4>
          <span class="ages">Ages 18 and over</span>
          <span class="ages-sml">18 and over</span>
        </div>
        <div>
          <h4>Children</h4>
          <span class="ages">Ages 17 and under</span>
          <span class="ages-sml">17 and under</span>
        </div>
        <span />
      </div>
      <div v-for="(accommodation, index) in value" :key="index" class="picker-item"
        :class="{ invalid: accommodation.invalidate }">
        <span class="name">#{{ index + 1 }}</span>
        <InputNumber v-model="accommodation.guests.adults" :inputId="`${index}-adults`" :min="1"
          :max="getMax(accommodation.type, index)" :disabled="accommodation.invalidate" showButtons
          buttonLayout="horizontal" />
        <InputNumber v-model="accommodation.guests.children" :inputId="`${index}-children`" :min="0"
          :max="getMax(accommodation.type, index, true)" :disabled="accommodation.invalidate" showButtons
          buttonLayout="horizontal" />
        <i class="pi pi-trash" :class="{ disabled: index === 0 && !accommodation.type }"
          @click="removeAccommodation(index)" />
      </div>
      <div class="picker-footer">
        <p>
          <span v-show="value.length === 4">Trying to book more than 4 rooms?</span>
          <span v-show="value.length === 4">Contact us and we'll help you out.</span>
        </p>
        <Button class="p-button-success p-button-outlined" label="Add Additional" :disabled="value.length === 4"
          @click="addAccommodation" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, defineProps, defineEmits, onMounted } from 'vue';

const props = defineProps({
  modelValue: {
    type: Object
  },
  disabled: {
    type: Boolean,
    default: false
  },
});

const emit = defineEmits(['update:modelValue']);

const guestspicker = ref();
const isOpen = ref(false);

const value = computed({
  get() {
    return props.modelValue;
  },
  set(val) {
    emit('update:modelValue', val);
  }
});

const total = computed(() => {
  let count = 0;
  value.value.forEach((item) => {
    count += item.guests.adults + item.guests.children;
  });
  return count;
});

function toggle() {
  isOpen.value = !isOpen.value;
}

function addAccommodation() {
  if (value.value.length < 4) {
    value.value.push({ type: undefined, price: 0, guests: { adults: 1, children: 0 } });
  }
}

function removeAccommodation(index) {
  if (index) {
    value.value.splice(index, 1);
  } else if (value.value[0].type) {
    value.value = [{ type: undefined, price: 0, guests: { adults: 1, children: 0 } }];
  }
}

function getMax(type, index, isChild = false) {
  let amount = 10;

  if (type) {
    if (type.main === 'standard') {
      if (type.sub === 'twin') {
        amount = 2;
      } else {
        amount = 6;
      }
    } else if (type.main === 'deluxe') {
      if (type.sub === 'king') {
        amount = 2;
      } else if (type.sub === 'twin') {
        amount = 2;
      } else if (type.sub === 'triple') {
        amount = 3;
      } else {
        amount = 4;
      }
    } else if (type.main === 'bunkroom') {
      amount = 4;
    }
  }

  if (index >= 0) {
    const { adults, children } = value.value[index].guests;

    if (isChild) {
      amount -= adults;
    } else {
      amount -= children;
    }
  }

  return amount;
}

onMounted(() => {
  window.addEventListener('mouseup', (e) => {
    if (isOpen.value) {
      if (!guestspicker.value.contains(e.target)) {
        isOpen.value = false;
      }
    }
  });
});
</script>

<style lang="scss" scoped>
.guests-picker {
  width: 100%;
  position: relative;

  .picker-button {
    display: flex;
    align-items: center;
    gap: 0.5em;
    width: 100%;
    height: 2.8em;
    border: 0.1em solid var(--surface-300);
    border-radius: 0.3em;
    background: var(--surface-card);

    .pi {
      display: flex;
      align-items: center;
      height: 100%;
      padding: 0.5em;
      border-right: 0.1em solid var(--surface-300);
    }

    span {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      padding: 0.1em;

      @media screen and (min-width: 2000px) {
        font-size: 1.1rem;
      }
    }

    &:not(.disabled) {
      cursor: pointer;

      &:hover,
      &.active {
        border-color: var(--surface-500);
        background: var(--surface-overlay);
        background: color-mix(in srgb, var(--surface-overlay) 70%, transparent);

        .pi {
          border-color: var(--surface-500);
        }
      }
    }

    &.disabled {
      opacity: 0.4;
    }
  }

  &.error .picker-button {
    border-color: var(--red-500);

    .pi {
      border-color: var(--red-500);
    }
  }
}

.picker-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5em;
  width: 28rem;
  padding: 0.5rem;
  background: var(--surface-ground);
  border: 0.1rem solid var(--surface-100);
  border-radius: 0.3rem;
  position: absolute;
  z-index: 2;
  top: calc(100% + 1.2rem);

  &:before {
    content: '';
    border-left: 1.25rem solid transparent;
    border-right: 1.25rem solid transparent;
    border-bottom: 1.25rem solid var(--surface-100);
    border-top: 1.25rem solid transparent;
    transform: translateX(-0.07rem) translateY(-1.25rem);
    position: absolute;
    top: -1.2rem;
    left: 1.5rem;
  }

  &:after {
    content: '';
    border-left: 1.2rem solid transparent;
    border-right: 1.2rem solid transparent;
    border-bottom: 1.2rem solid var(--surface-ground);
    border-top: 1.2rem solid transparent;
    transform: translateY(-1.06rem);
    position: absolute;
    top: -1.2rem;
    left: 1.5rem;
  }

  .close {
    display: none;
    color: var(--text-color);
    cursor: pointer;
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;

    &:hover {
      color: var(--theme-hover);
    }
  }

  .ages-sml {
    display: none;
  }

  @media screen and (max-width: 700px) {
    padding-top: 3rem;
    width: 90%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    &:before,
    &:after {
      display: none;
    }

    .close {
      display: unset;
    }

    .ages {
      display: none;
    }

    .ages-sml {
      display: unset;
    }
  }

  .picker-header,
  .picker-item {
    display: grid;
    grid-template-columns: 0.3fr 2fr 2fr 0.5fr;
    grid-gap: 0.5em;
    width: 100%;
    font-size: 0.9em;
  }

  .picker-header div {
    display: flex;
    flex-direction: column;
    gap: 0.2em;

    span,
    h4 {
      margin: 0;
      text-align: center;
    }
  }

  .picker-item {
    &.invalid .name {
      color: var(--red-500);
    }

    .name {
      display: flex;
      align-items: center;
    }

    .pi {
      display: flex;
      align-items: center;
      justify-content: center;

      &:not(.disabled) {
        cursor: pointer;

        &:hover {
          color: var(--theme-hover);
        }
      }

      &.disabled {
        opacity: 0.4;
      }
    }
  }

  .picker-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.5em;
    width: 100%;
    margin-top: 0.5em;

    p {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 0;
      font-size: 0.9em;

      @media screen and (max-width: 768px) {
        text-align: left;

        :first-child {
          margin-bottom: 0.25rem;
        }
      }
    }
  }

  :deep(.p-inputnumber) {
    input {
      width: 100%;
      height: 2.4em;
      font-size: 0.9em;
    }
  }
}
</style>
