<script setup>
import { ref, computed, defineProps, defineEmits } from 'vue';
import * as datefns from 'date-fns';
import ExpandableImage from '@/components/common/ExpandableImage.vue';

const props = defineProps({
  modelValue: {
    type: Object
  },
  arrival: {
    type: Date,
    required: true
  },
  departure: {
    type: Date,
    required: true
  },
  available: {
    type: Object,
    required: true
  },
  error: {
    type: String,
    default: ''
  }
});

const emit = defineEmits(['update:modelValue']);

const splideOptions = ref({
  type: 'loop',
  role: 'group',
  autoplay: true,
  gap: '1rem',
  perPage: 1,
  speed: 800,
  interval: 2500,
  resetProgress: true,
});

const compAvailable = computed(() => {
  const output = {
    invalid: props.available.invalid,
    standard: {
      twin: props.available.standard.twin,
      family: props.available.standard.family
    },
    deluxe: {
      king: props.available.deluxe.king,
      twin: props.available.deluxe.twin,
      triple: props.available.deluxe.triple,
      family: props.available.deluxe.family
    },
    bunkroom: props.available.bunkroom,
    poweredcamping: props.available.poweredcamping
  };

  function clamp(num, min) {
    return Math.max(num, min);
  }

  value.value.forEach((accommodation) => {
    if (accommodation.type && accommodation.type !== 'NEW') {
      if (accommodation.type.main === 'standard') {
        if (accommodation.type.sub === 'twin') {
          output.standard.twin = clamp(output.standard.twin - 1, 0);
        } else {
          output.standard.family = clamp(output.standard.family - 1, 0);
        }
      } else if (accommodation.type.main === 'deluxe') {
        if (accommodation.type.sub === 'king') {
          output.deluxe.king = clamp(output.deluxe.king - 1, 0);
        } else if (accommodation.type.sub === 'twin') {
          output.deluxe.twin = clamp(output.deluxe.twin - 1, 0);
        } else if (accommodation.type.sub === 'triple') {
          output.deluxe.triple = clamp(output.deluxe.triple - 1, 0);
        } else {
          output.deluxe.family = clamp(output.deluxe.family - 1, 0);
        }
      } else if (accommodation.type.main === 'bunkroom') {
        output.bunkroom = clamp(output.bunkroom - 1, 0);
      } else {
        output.poweredcamping = clamp(output.poweredcamping - 1, 0);
      }
    }
  });

  output.standard.total = output.standard.twin + output.standard.family;
  output.standard.total_actual = props.available.standard.family + props.available.standard.twin;
  output.deluxe.total = output.deluxe.king + output.deluxe.twin + output.deluxe.triple + output.deluxe.family;
  output.deluxe.total_actual = props.available.deluxe.king + props.available.deluxe.twin + props.available.deluxe.triple + props.available.deluxe.family;

  return output;
});

const value = computed({
  get() {
    return props.modelValue;
  },
  set(val) {
    emit('update:modelValue', val);
  }
});

const current = computed(() => {
  let output = value.value.find((item) => item.type === undefined);
  if (!output) {
    output = { type: 'NEW', price: 0, guests: { adults: 1, children: 0 }, isPrevious: false };
  }
  return output;
});

const currentGuests = computed(() => {
  if (current.value) {
    return current.value.guests.adults + current.value.guests.children;
  }
  return 0;
});

const nights = computed(() => {
  const diff = datefns.differenceInCalendarDays(props.departure, props.arrival);
  if (diff > 0) {
    return diff;
  } else {
    return 1;
  }
});

const unselected = computed(() => {
  return value.value.filter((item) => item.type === undefined).length;
});

function aboveMax(max) {
  return currentGuests.value > max;
}

function select(type, subtype) {
  const temp = { ...current.value };
  const isNew = temp.type === 'NEW';

  temp.type = { main: type, sub: subtype };
  const cur = value.value.indexOf(current.value);
  value.value[cur] = temp;

  if (isNew) {
    value.value.push(temp);
  }
}
</script>

