<template>
  <div ref="datepicker" class="date-picker">
    <div class="picker-button" :class="{ 'active': isOpen, 'disabled': disabled }" @click="toggle">
      <i class="pi pi-calendar" />
      <span>{{ isEnd ? (end ? datefns.format(end, 'PPPP') : placeholder) : (value ? datefns.format(value, 'PPPP') :
      placeholder) }}</span>
    </div>
    <div v-if="isOpen" class="picker-modal">
      <i class="close pi pi-times" @click="toggle" />
      <div v-show="loading" class="loader">
        <i class="pi pi-spinner pi-spin" />
      </div>
      <div class="expanded" :class="{ 'blur': loading }">
        <div v-for="(month, index) in months" :key="index" :id="`month-${index}`" class="month">
          <div class="controls">
            <i v-if="index === 0" class="btn pi pi-chevron-left"
              :class="{ disabled: datefns.isThisMonth(months[0].days[0].date) || loading }" @click="prev" />
            <div v-else />
            <h2>{{ month.name }} {{ month.year }}</h2>
            <i class="btn pi pi-chevron-right" :class="{ disabled: loading }" @click="next" />
          </div>
          <div class="calendar">
            <div class="header">
              <h3>{{ getDayName('Monday') }}</h3>
              <h3>{{ getDayName('Tuesday') }}</h3>
              <h3>{{ getDayName('Wednesday') }}</h3>
              <h3>{{ getDayName('Thursday') }}</h3>
              <h3>{{ getDayName('Friday') }}</h3>
              <h3>{{ getDayName('Saturday') }}</h3>
              <h3>{{ getDayName('Sunday') }}</h3>
            </div>
            <div class="body">
              <div v-for="n in 35" :key="`${index}-${n}`" :id="`day-${n}`" class="day" :class="addClasses(index, n)"
                @click="selectDay(index, n)" @mouseover="mouseover(index, n)">
                <h4>{{ calculateDayOfMonth(index, n) }}</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="invalid-note" :class="{ 'blur': loading }">
        <div class="invalid" />
        <span>Represents invalid dates</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, defineProps, defineEmits, defineExpose, onBeforeMount, onMounted } from 'vue';
import * as datefns from 'date-fns';
import { storeToRefs } from 'pinia';
import { useCalendarStore } from '@/stores/calendar';

const calendarStore = useCalendarStore();
const {
  loading,
  monthCount,
  months
} = storeToRefs(calendarStore);
const {
  calculateDayOfMonth,
  getData,
  next,
  prev,
  reset
} = useCalendarStore();

const props = defineProps({
  modelValue: {
    type: Date
  },
  end: {
    type: Date
  },
  useRange: {
    type: Boolean,
    default: false
  },
  isEnd: {
    type: Boolean,
    default: false
  },
  disabled: {
    type: Boolean,
    default: false
  },
  placeholder: {
    type: String,
    default: ''
  }
});

const emit = defineEmits(['update:modelValue', 'update:end']);

const datepicker = ref();
const isOpen = ref(false);

const value = computed({
  get() {
    return props.modelValue;
  },
  set(val) {
    emit('update:modelValue', val);
  }
});

const endValue = computed({
  get() {
    return props.end;
  },
  set(val) {
    emit('update:end', val);
  }
});

function addClasses(index, n) {
  return {
    'diff-month': n - months.value[index].offset > months.value[index].count || n <= months.value[index].offset,
    today: getData(index, n) ? datefns.isToday(getData(index, n).date) : false,
    invalid: getData(index, n) ? getData(index, n)?.invalid : false,
    end: datefns.isEqual(getData(index, n).date, value.value) || (props.useRange && datefns.isEqual(getData(index, n).date, endValue.value)),
    middle: props.useRange && (datefns.isAfter(getData(index, n).date, value.value) && datefns.isBefore(getData(index, n).date, endValue.value)),
    start: props.useRange && datefns.isEqual(getData(index, n).date, value.value),
    stop: props.useRange && datefns.isEqual(getData(index, n).date, endValue.value)
  };
}

function getDayName(day) {
  return day.substring(0, 2);
}

async function Reset() {
  monthCount.value = 2;

  await reset();
}

function toggle() {
  isOpen.value = !isOpen.value;
}

function selectDay(index, n) {
  if (!props.useRange) {
    value.value = getData(index, n).n;
  } else if (!datefns.isEqual(value.value, endValue.value)) {
    value.value = getData(index, n).date;
    endValue.value = getData(index, n).date;
  } else if (datefns.isBefore(getData(index, n).date, value.value)) {
    value.value = getData(index, n).date;
    endValue.value = getData(index, n).date;
  } else {
    endValue.value = getData(index, n).date;
  }
}

function mouseover(index, n) {
  if (props.useRange && datefns.isEqual(value.value, endValue.value)) {
    const date = getData(index, n).date;

    for (let i = 0; i < months.value.length; i++) {
      for (let j = 1; j <= 35; j++) {
        const day = getData(i, j).date;

        document.querySelector(`#month-${i} #day-${j}`).classList.remove('middle');
        if (!datefns.isEqual(value.value, day)) {
          document.querySelector(`#month-${i} #day-${j}`).classList.remove('end');
        }

        if (datefns.isEqual(day, date)) {
          document.querySelector(`#month-${i} #day-${j}`).classList.add('end');
        } else if (datefns.isAfter(day, value.value) && datefns.isBefore(day, date)) {
          document.querySelector(`#month-${i} #day-${j}`).classList.add('middle');
        }
      }
    }
  }
}

defineExpose({ reset: Reset });

onBeforeMount(() => {
  if (!datefns.isValid(value.value)) {
    value.value = datefns.startOfTomorrow();
  }

  if (!datefns.isValid(endValue.value)) {
    endValue.value = datefns.addDays(datefns.startOfTomorrow(), 1);
  }
});

onMounted(async () => {
  if (!loading.value) {
    monthCount.value = 2;
    await reset();
  }

  window.addEventListener('mouseup', (e) => {
    if (isOpen.value) {
      if (!datepicker.value.contains(e.target)) {
        isOpen.value = false;
      }
    }
  });
});
</script>

<style lang="scss" scoped>
.date-picker {
  width: 100%;
  position: relative;

  .picker-modal {
    padding: 0.5rem;
    background: var(--surface-ground);
    border: 0.1rem solid var(--surface-100);
    border-radius: 0.3rem;
    position: absolute;
    z-index: 2;
    top: calc(100% + 1.2rem);

    &:before {
      content: '';
      border-left: 1.25rem solid transparent;
      border-right: 1.25rem solid transparent;
      border-bottom: 1.25rem solid var(--surface-100);
      border-top: 1.25rem solid transparent;
      transform: translateX(-0.07rem) translateY(-1.25rem);
      position: absolute;
      top: -1.2rem;
      left: 1.5rem;
    }

    &:after {
      content: '';
      border-left: 1.2rem solid transparent;
      border-right: 1.2rem solid transparent;
      border-bottom: 1.2rem solid var(--surface-ground);
      border-top: 1.2rem solid transparent;
      transform: translateY(-1.06rem);
      position: absolute;
      top: -1.2rem;
      left: 1.5rem;
    }

    .close {
      display: none;
      color: var(--text-color);
      cursor: pointer;
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;

      &:hover {
        color: var(--theme-hover);
      }
    }

    @media screen and (max-width: 700px) {
      padding-top: 3rem;
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);

      &:before,
      &:after {
        display: none;
      }

      .close {
        display: unset;
      }
    }

    .loader {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background: rgba(20, 20, 20, 0.1);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;

      .pi {
        font-size: 4rem;
      }
    }
  }

  .picker-button {
    display: flex;
    align-items: center;
    gap: 0.5em;
    width: 100%;
    height: 2.8em;
    border: 0.1em solid var(--surface-300);
    border-radius: 0.3em;
    background: var(--surface-card);

    .pi {
      display: flex;
      align-items: center;
      height: 100%;
      padding: 0.5em;
      border-right: 0.1em solid var(--surface-300);
    }

    span {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      padding: 0.1em;
    }

    &:not(.disabled) {
      cursor: pointer;

      &:hover,
      &.active {
        border-color: var(--surface-500);
        background: var(--surface-overlay);
        background: color-mix(in srgb, var(--surface-overlay) 70%, transparent);

        .pi {
          border-color: var(--surface-500);
        }
      }
    }

    &.disabled {
      opacity: 0.4;
    }
  }

  &.error .picker-button {
    border-color: var(--red-500);

    .pi {
      border-color: var(--red-500);
    }
  }
}

.invalid-note {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 2.5rem;

  &.blur {
    filter: blur(0.15rem);
  }

  span {
    font-size: 0.75rem;

    @media screen and (min-width: 2000px) {
      font-size: 1rem;
    }
  }

  .invalid {
    position: relative;
    width: 0.75rem;
    height: 0.75rem;

    @media screen and (min-width: 2000px) {
      width: 1rem;
      height: 1rem;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><line x1='0' y1='0' x2='100' y2='100' stroke='rgba(220, 0, 0, 0.6)' vector-effect='non-scaling-stroke'/><line x1='0' y1='100' x2='100' y2='0' stroke='rgba(220, 0, 0, 0.6)' vector-effect='non-scaling-stroke'/></svg>");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100% 100%, auto;
    }
  }
}

.expanded {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  position: relative;

  @media screen and (min-width: 701px) {
    #month-0 .pi-chevron-right {
      opacity: 0;
    }
  }

  &.blur .month {
    filter: blur(0.15rem);
  }

  .month {
    --month-width: 16rem;
    --month-height: 15rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    width: var(--month-width);
    height: var(--month-height);
    font-size: 0.9rem;

    @media screen and (min-width: 2000px) {
      --month-width: 25rem;
      --month-height: 23rem;
      font-size: 1.2rem;
    }

    @media screen and (max-width: 700px) {
      font-size: 0.75rem;

      &#month-1 {
        display: none;
      }
    }

    .controls {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: var(--month-width);

      h2 {
        margin: 0;
      }

      .pi {
        &:not(.disabled) {
          cursor: pointer;

          &:hover {
            color: var(--theme-hover);
          }
        }

        &.disabled {
          opacity: 0.4;
        }
      }
    }

    .calendar {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 0.5rem;
      width: var(--month-width);

      .header {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 0;
        // grid-gap: 0.5rem;
        width: var(--month-width);

        h3 {
          margin: 0;
          text-align: center;
        }
      }

      .body {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        // grid-gap: 0.5rem;
        grid-row-gap: 0.25rem;
        width: 100%;

        div {
          display: flex;
          align-items: center;
          justify-content: center;
          aspect-ratio: 1 / 1;
          border-radius: 0.1rem;
          cursor: pointer;

          h4 {
            margin: 0;
          }

          &.invalid,
          &.today {
            position: relative;

            &:before {
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><line x1='0' y1='0' x2='100' y2='100' stroke='rgba(220, 0, 0, 0.6)' vector-effect='non-scaling-stroke'/><line x1='0' y1='100' x2='100' y2='0' stroke='rgba(220, 0, 0, 0.6)' vector-effect='non-scaling-stroke'/></svg>");
              background-repeat: no-repeat;
              background-position: center center;
              background-size: 100% 100%, auto;
            }
          }

          &.end {
            background: color-mix(in srgb, var(--green-800) 80%, transparent);

            &.start {
              border-top-right-radius: 0;
              border-bottom-right-radius: 0;
            }

            &.stop {
              border-top-left-radius: 0;
              border-bottom-left-radius: 0;
            }
          }

          &.middle {
            background: color-mix(in srgb, var(--green-600) 80%, transparent);
            border-radius: 0;
          }

          &.diff-month h4 {
            opacity: 0.3;
          }

          &:hover {
            background: color-mix(in srgb, var(--maskbg) 60%, transparent);

            h4 {
              color: var(--theme-hover);
            }

            &.end {
              background: color-mix(in srgb, var(--green-600) 60%, transparent);
            }

            &.middle {
              background: color-mix(in srgb, var(--green-400) 60%, transparent);
            }
          }
        }
      }
    }
  }
}
</style>
