import { ref } from 'vue';
import { defineStore } from 'pinia';
import * as datefns from 'date-fns';
import fetchPlus from '@/utilities/FetchPlus';
import getHost from '@/utilities/GetHost';

export const useCalendarStore = defineStore('calendar', () => {
  const loading = ref(false);
  const monthCount = ref(1);
  const months = ref([]);
  const nextBuffer = ref([]);
  const nextMonth = ref({});
  const prevBuffer = ref([]);
  const prevMonth = ref({});

  function calculateDayOfMonth(index, n) {
    if (n <= months.value[index].offset) {
      if (index === 0) {
        return prevMonth.value.count - (months.value[index].offset - n);
      } else {
        return months.value[index - 1].count - (months.value[index].offset - n);
      }
    } else if (n - months.value[index].offset > months.value[index].count) {
      return n - months.value[index].offset - months.value[index].count;
    } else {
      return n - months.value[index].offset;
    }
  }

  function getData(index, n) {
    if (n <= months.value[index].offset) {
      if (index === 0) {
        return prevMonth.value.days[prevMonth.value.count - (months.value[index].offset - n) - 1];
      } else {
        return months.value[index - 1].days[months.value[index - 1].count - (months.value[index].offset - n) - 1];
      }
    } else if (n - months.value[index].offset > months.value[index].count) {
      if (index === months.value.length - 1) {
        return nextMonth.value.days[n - months.value[index].offset - months.value[index].count - 1];
      } else {
        return months.value[index + 1].days[n - months.value[index].offset - months.value[index].count - 1];
      }
    } else {
      return months.value[index].days[n - months.value[index].offset - 1];
    }
  }
  
  async function next() {
    if (datefns.compareAsc(nextMonth.value.days[0].date, datefns.addYears(new Date(), 2)) === -1) {
      loading.value = true;
      let month;
      if (nextBuffer.value.length) {
        month = { ...nextBuffer.value.shift() };
      } else {
        month = await createMonth(datefns.addMonths(nextMonth.value.days[10].date, 1));
      }
      
      prevBuffer.value.unshift({ ...prevMonth.value });
      if (prevBuffer.value.length > 10) {
        prevBuffer.value.pop();
      }

      prevMonth.value = { ...months.value.shift() };
      months.value.push({ ...nextMonth.value });
      nextMonth.value = { ...month };
      loading.value = false;
    }
  }

  async function prev() {
    if (!datefns.isThisMonth(months.value[0].days[0].date)) {
      loading.value = true;
      let month;
      if (prevBuffer.value.length) {
        month = { ...prevBuffer.value.shift() };
      } else {
        month = await createMonth(datefns.subMonths(prevMonth.value.days[10].date, 1));
      }

      nextBuffer.value.unshift({ ...nextMonth.value });
      if (nextBuffer.value.length > 10) {
        nextBuffer.value.pop();
      }

      nextMonth.value = { ...months.value.pop() };
      months.value.unshift({ ...prevMonth.value });
      prevMonth.value = { ...month };
      loading.value = false;
    }
  }

  async function reset() {
    loading.value = true;
    
    const today = new Date();
    months.value = [];

    prevMonth.value = await createMonth(datefns.subMonths(today, 1));
    
    for (let i = 0; i < monthCount.value; i++) {
      months.value.push(await createMonth(datefns.addMonths(today, i)));
    }

    nextMonth.value = await createMonth(datefns.addMonths(today, monthCount.value));
    loading.value = false;
  }

  async function createMonth(date) {
    return new Promise((resolve) => {
      // const year = datefns.getYear(date);
      // const month = datefns.getMonth(date);
      // const som = new Date(`${year} ${month} 1`);
      const som = datefns.startOfMonth(date);
      const year = datefns.getYear(som);
      const month = datefns.getMonth(som);

      const temp = {
        name: datefns.format(som, 'MMMM'),
        year,
        offset: datefns.getDay(som) - 1,
        count: datefns.getDaysInMonth(som),
        days: []
      };

      if (datefns.isBefore(som, datefns.startOfMonth(new Date()))) {
        for (let i = 0; i < temp.count; i++) {
          const day = datefns.addDays(som, i);

          temp.days.push({
            date: day,
            invalid: true
          });
        }
        
        resolve(temp);
      } else {
        const results = [];
        const duration = datefns.getDaysInMonth(som);

        for (let i = 0; i < duration; i++) {
          results.push(Math.random() > 0.8);
        }

        setTimeout(() => {
          for (let i = 0; i < temp.count; i++) {
            const day = datefns.addDays(som, i);
            let invalid = true;

            if (!datefns.isBefore(date, datefns.endOfDay(new Date()))) {
              invalid = results[i];
            }

            temp.days.push({
              date: day,
              invalid
            });
          }

          resolve(temp);
        }, Math.floor(Math.random() * (7500 - 801) - 800));
        // fetchPlus(`${getHost()}/bookings/availability/calendar`, 15, {
        //   method: 'POST',
        //   headers: {
        //     'Content-Type': 'application/json'
        //   },
        //   body: JSON.stringify({ year, month })
        // }).then((response) => {
        //   if (!response.error) {
        //     for (let i = 0; i < temp.count; i++) {
        //       const day = datefns.addDays(som, i);
        //       let invalid = true;

        //       if (!datefns.isBefore(day, datefns.endOfDay(new Date()))) {
        //         invalid = response.result.data[i];
        //       }

        //       temp.days.push({
        //         date: day,
        //         invalid
        //       });
        //     }
        //   } else {
        //     console.error('Availablity retrieval error:', response.result.message);

        //     for (let i = 0; i < temp.count; i++) {
        //       const day = datefns.addDays(som, i);

        //       temp.days.push({
        //         date: day,
        //         invalid: true
        //       });
        //     }
        //   }

        //   resolve(temp);
        // }).catch((err) => {
        //   console.error('Availablity retrieval error:', err);

        //   for (let i = 0; i < temp.count; i++) {
        //     const day = datefns.addDays(som, i);

        //     temp.days.push({
        //       date: day,
        //       invalid: true
        //     });
        //   }

        //   resolve(temp);
        // });
      }
    });
  }

  return {
    loading,
    monthCount,
    months,
    nextMonth,
    prevMonth,
    calculateDayOfMonth,
    getData,
    next,
    prev,
    reset
  };
});
