<template>
  <div v-resize="resize" class="booking">
    <div class="header">
      <h1>Accommodation Booking</h1>
      <router-link v-if="step === 0" class="link" to="/accommodation">
        <i class="pi pi-angle-double-left" />
        Back
      </router-link>
    </div>
    <div v-show="loading" class="content">
      <div class="loading">
        <section id="section-loading">
          <div class="form-section" id="fsection-accommodation">
            <div class="form-item">
              <div class="name">
                <Skeleton height="0.95rem" style="width: 12.5rem !important;" />
              </div>
              <Skeleton height="2.8rem" />
            </div>
            <div class="form-item">
              <div class="name">
                <Skeleton height="0.95rem" style="width: 5.6rem !important;" />
              </div>
              <Skeleton height="2.8rem" />
            </div>
            <div class="form-item">
              <div class="name">
                <Skeleton height="0.95rem" style="width: 7.1rem !important;" />
              </div>
              <Skeleton height="2.8rem" />
            </div>
          </div>
          <Skeleton height="1.4rem" width="16rem" style="margin-top: 0.5rem" />
          <Skeleton height="21rem" style="margin-top: 0.5rem" />
        </section>
      </div>
      <Skeleton height="27.8rem" style="margin-top: 1.4rem" />
    </div>
    <div v-show="!loading" class="content" :class="{ processing: step >= 4 }">
      <form v-show="step === 0" @submit="onAccommodationSubmit">
        <section id="section-accommodation">
          <div class="accommodation-sml">
            <input class="accommodation-toggle" type="checkbox" />
            <div class="brief">
              <i class="pi pi-calendar" />
              {{ accommodationBrief }}
              <i class="pi pi-chevron-down" />
            </div>
            <div class="full">
              <i class="pi pi-chevron-up" />
              <div class="form-item" id="item-guests">
                <div class="name">
                  <span>Accommodation and Guests</span>
                  <span class="required">*</span>
                  <small class="p-error" id="accommodation-error">{{ accommodationError || '&nbsp;' }}</small>
                </div>
                <GuestsPicker v-model="accommodation" id="accommodation" :class="{ error: accommodationError }"
                  aria-describedby="accommodation-error" />
              </div>
              <div class="dates">
                <div class="form-item" id="item-arrival">
                  <div class="name">
                    <span>Arrival Date</span>
                    <span class="required">*</span>
                    <small class="p-error" id="arrival-error">{{ arrivalError || '&nbsp;' }}</small>
                  </div>
                  <DatePicker v-model="arrival" v-model:end="departure" id="arrival" :class="{ error: arrivalError }"
                    useRange :numberOfMonths="2" aria-describedby="arrival-error" />
                </div>
                <div class="form-item" id="item-departure">
                  <div class="name">
                    <span>Departure Date</span>
                    <span class="required">*</span>
                    <small class="p-error" id="departure-error">{{ departureError || '&nbsp;' }}</small>
                  </div>
                  <DatePicker v-model="arrival" v-model:end="departure" id="departure"
                    :class="{ error: departureError }" useRange :numberOfMonths="2" isEnd
                    aria-describedby="arrival-error" />
                </div>
              </div>
            </div>
          </div>
          <div class="form-section" id="fsection-accommodation">
            <div class="form-item" id="item-guests">
              <div class="name">
                <span>Accommodation and Guests</span>
                <span class="required">*</span>
                <small class="p-error" id="accommodation-error">{{ accommodationError || '&nbsp;' }}</small>
              </div>
              <GuestsPicker v-model="accommodation" id="accommodation" :class="{ error: accommodationError }"
                aria-describedby="accommodation-error" />
            </div>
            <div class="form-item" id="item-arrival">
              <div class="name">
                <span>Arrival Date</span>
                <span class="required">*</span>
                <small class="p-error" id="arrival-error">{{ arrivalError || '&nbsp;' }}</small>
              </div>
              <DatePicker v-model="arrival" v-model:end="departure" id="arrival" :class="{ error: arrivalError }"
                useRange :numberOfMonths="2" aria-describedby="arrival-error" />
            </div>
            <div class="form-item" id="item-departure">
              <div class="name">
                <span>Departure Date</span>
                <span class="required">*</span>
                <small class="p-error" id="departure-error">{{ departureError || '&nbsp;' }}</small>
              </div>
              <DatePicker v-model="arrival" v-model:end="departure" id="departure" :class="{ error: departureError }"
                useRange :numberOfMonths="2" isEnd aria-describedby="arrival-error" />
            </div>
          </div>
          <h3>Select Your Accommodation</h3>
          <AccommodationSelect v-model="accommodation" :arrival="formData.arrival ?? arrival"
            :departure="formData.departure ?? departure" :available="available" :error="accommodationError" />
          <Button ref="accommodationNext" class="hidden" type="submit" />
        </section>
      </form>
      <form v-show="step === 1" @submit="onPackageSubmit">
        <section id="section-extras">
          <div class="header">
            <h1>Enhance Your Stay</h1>
          </div>
          <div class="buttons">
            <Button ref="extraBack" class="p-button-outlined" label="Back" @click="back" />
            <Button ref="extraNext" class="p-button-success" type="submit" label="Next" />
          </div>
        </section>
      </form>
      <form v-show="!loading && step === 2" @submit="onPersonalSubmit">
        <section id="section-contact">
          <div class="header">
            <h3>Contact Information</h3>
          </div>
          <div class="note">
            <span class="required">*</span>
            <span>Indicates required fields</span>
          </div>
          <div class="form-section" id="fsection-name">
            <div class="form-item" id="item-prefix">
              <div class="name">
                <span>Prefix</span>
                <small class="p-error" id="prefix-error">{{ prefixError || '&nbsp;' }}</small>
              </div>
              <Dropdown v-model="prefix" inputId="prefix" :options="prefixes" placeholder="Prefix"
                :class="{ 'p-invalid': prefixError }" aria-describedby="prefix-error" />
            </div>
            <div class="form-item" id="item-firstname">
              <div class="name">
                <span>First Name</span>
                <span class="required">*</span>
                <small class="p-error" id="firstname-error">{{ firstnameError || '&nbsp;' }}</small>
              </div>
              <InputText v-model="firstname" id="firstname" :class="{ 'p-invalid': firstnameError }"
                aria-describedby="firstname-error" />
            </div>
          </div>
          <div class="form-item" id="item-lastname">
            <div class="name">
              <span>Last Name</span>
              <span class="required">*</span>
              <small class="p-error" id="lastname-error">{{ lastnameError || '&nbsp;' }}</small>
            </div>
            <InputText v-model="lastname" id="lastname" :class="{ 'p-invalid': lastnameError }"
              aria-describedby="lastname-error" />
          </div>
          <div class="form-item" id="item-email">
            <div class="name">
              <span>Email Address</span>
              <span class="required">*</span>
              <small class="p-error" id="email-error">{{ emailError || '&nbsp;' }}</small>
            </div>
            <InputText v-model="email" id="email" :class="{ 'p-invalid': emailError }" aria-describedby="email-error" />
          </div>
          <div class="form-item" id="item-email">
            <div class="name">
              <span>Confirm Email Address</span>
              <span class="required">*</span>
              <small class="p-error" id="confirmemail-error">{{ confirmEmailError || '&nbsp;' }}</small>
            </div>
            <InputText v-model="confirmEmail" id="confirmEmail" :class="{ 'p-invalid': confirmEmailError }"
              aria-describedby="confirmEmail-error" />
          </div>
          <div class="form-item" id="item-phone">
            <div class="name">
              <span>Phone Number</span>
              <span class="required">*</span>
              <small class="p-error" id="phone-error">{{ phoneError || '&nbsp;' }}</small>
            </div>
            <InputText v-model="phone" id="phone" :class="{ 'p-invalid': phoneError }" aria-describedby="phone-error" />
          </div>
          <div class="buttons">
            <Button ref="contactBack" class="p-button-outlined" label="Back" @click="back" />
            <Button ref="contactNext" class="p-button-success" label="Next" type="submit" />
          </div>
        </section>
      </form>
      <form v-show="step >= 3" @submit="onBillingSubmit">
        <section v-show="step === 3" id="section-billing">
          <div class="header">
            <h3>Billing Information</h3>
          </div>
          <div class="form-section" id="note">
            <div class="note">
              <span class="required">*</span>
              <span>Indicates required fields</span>
            </div>
            <div class="note" id="billing">
              <span>Billing address is used for verification</span>
            </div>
          </div>
          <div class="form-item" id="item-card">
            <div class="name">
              <span>Card Details</span>
              <span class="required">*</span>
              <small class="p-error" id="card-error">{{ cardError || '&nbsp;' }}</small>
            </div>
            <div v-if="isSecure" id="card-container" :class="{ error: cardError }" aria-describedby="card-error"></div>
            <Skeleton v-else class="sq-card-component" style="border: 1px solid rgba(255, 255, 255, 0.3)" />
          </div>
          <div class="form-item" id="item-address1">
            <div class="name">
              <span>Address Line 1</span>
              <span class="required">*</span>
              <small class="p-error" id="address1-error">{{ address1Error || '&nbsp;' }}</small>
            </div>
            <InputText v-model="address1" id="address1" :class="{ 'p-invalid': address1Error }" :disabled="processing"
              aria-describedby="address1-error" />
          </div>
          <div class="form-item" id="item-address2">
            <div class="name">
              <span>Address Line 2</span>
              <small class="p-error" id="address2-error">{{ address2Error || '&nbsp;' }}</small>
            </div>
            <InputText v-model="address2" id="address2" :class="{ 'p-invalid': address2Error }" :disabled="processing"
              aria-describedby="address2-error" />
          </div>
          <div class="form-section" id="location1">
            <div class="form-item" id="item-country">
              <div class="name">
                <span>Country</span>
                <span class="required">*</span>
                <small class="p-error" id="country-error">{{ countryError || '&nbsp;' }}</small>
              </div>
              <Dropdown v-model="country" id="country" :options="sortedCountries" optionValue="code" optionLabel="name"
                filter :class="{ 'p-invalid': countryError }" :disabled="processing" aria-describedby="country-error" />
            </div>
            <div class="form-item" id="item-state">
              <div class="name">
                <span>State</span>
                <span class="required">*</span>
                <small class="p-error" id="state-error">{{ stateError || '&nbsp;' }}</small>
              </div>
              <Dropdown v-model="state" id="state" :options="sortedStates" optionLabel="name" optionValue="code" filter
                :class="{ 'p-invalid': stateError }" :disabled="processing || !country || !sortedStates.length"
                aria-describedby="state-error" />
            </div>
          </div>
          <div class="form-section" id="location2">
            <div class="form-item" id="item-city">
              <div class="name">
                <span>City</span>
                <span class="required">*</span>
                <small class="p-error" id="city-error">{{ cityError || '&nbsp;' }}</small>
              </div>
              <InputText v-model="city" id="city" :class="{ 'p-invalid': cityError }" :disabled="processing"
                aria-describedby="city-error" />
            </div>
            <div class="form-item" id="item-postal">
              <div class="name">
                <span>Zip / Postal Code</span>
                <small class="p-error" id="postal-error">{{ postalError || '&nbsp;' }}</small>
              </div>
              <InputText v-model="postal" id="postal" :class="{ 'p-invalid': postalError }" :disabled="processing"
                aria-describedby="postal-error" />
            </div>
            <div class="form-item" id="item-terms">
              <div class="name">
                <span>Please do not check this checkbox</span>
                <small class="p-error" id="terms-error">&nbsp;</small>
              </div>
              <input v-model="honeypot" type="checkbox" name="terms" id="terms" value="1"
                aria-describedby="terms-error" />
            </div>
          </div>
          <div class="recaptcha-note">
            <span>
              This site is protected by reCAPTCHA and the Google
              <a href="https://policies.google.com/privacy">Privacy Policy</a> and
              <a href="https://policies.google.com/terms">Terms of Service</a> apply.
            </span>
          </div>
          <div class="buttons">
            <Button ref="billingBack" class="p-button-outlined" label="Back" :disabled="processing" @click="back" />
            <Button ref="billingBook" class="p-button-success" label="Book" type="submit" :disabled="processing" />
          </div>
        </section>
        <section v-show="step === 4" id="section-summary">
          <div class="header">
            <h3>Booking Summary</h3>
          </div>
          <div class="summary-content">
            <div class="summary-info">
              <div class="form-item">
                <div class="name">
                  <h4>Arrival Date:</h4>
                </div>
                <span>{{ datefns.format(formData?.arrival ?? new Date(), 'PPPP') }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Departure Date:</h4>
                </div>
                <span>{{ datefns.format(formData?.departure ?? new Date(), 'PPPP') }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Nights:</h4>
                </div>
                <span>{{ clamp(datefns.differenceInCalendarDays(formData?.departure ?? new Date(), formData?.arrival ??
    new Date()), 1) }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Total Guests:</h4>
                </div>
                <span>{{ summary.guests }} {{ summary.guests === 1 ? 'Guest' : 'Guests' }} ({{ formData.accommodations ?
    formData.accommodations.reduce((n, { guests }) => n + guests.adults, 0) : 1 }} {{
    (formData.accommodations ? formData.accommodations.reduce((n, { guests }) => n + guests.adults, 0) :
      1) === 1 ? 'Adult' : 'Adults' }}, {{ formData.accommodations ? formData.accommodations.reduce((n, {
    guests }) => n + guests.children, 0) : 0 }} {{ (formData.accommodations ?
    formData.accommodations.reduce((n, { guests }) => n + guests.children, 0) : 0) === 1 ? 'Child' :
    'Children' }})</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>First Name:</h4>
                </div>
                <span>{{ formData?.firstname ?? '' }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Last Name:</h4>
                </div>
                <span>{{ formData?.lastname ?? '' }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Email Address:</h4>
                </div>
                <span>{{ formData?.email ?? '' }}</span>
              </div>
              <div class="form-item">
                <div class="name">
                  <h4>Phone Number:</h4>
                </div>
                <span>{{ formData?.phone ?? '' }}</span>
              </div>
            </div>
            <div class="summary-billing">
              <div class="summary-items">
                <h4>Name</h4>
                <h4>Count</h4>
                <h4>Price</h4>
                <template v-for="(room, index) in formData.accommodations" :key="`accommodation-${index}`">
                  <span>{{ getRoomName(room.type) }} - {{ room.guests.adults + room.guests.children }} {{
    room.guests.adults + room.guests.children === 1 ? 'Guest' : 'Guests' }} ({{ room.guests.adults }} {{
    room.guests.adults === 1 ? 'Adult' : 'Adults' }}, {{ room.guests.children }} {{ room.guests.children
    === 1 ? 'Child' : 'Children' }})</span>
                  <span>1</span>
                  <span>AUD ${{ room.price.toFixed(2) }}</span>
                  <span v-if="room.note" class="sml-note">- {{ room.note }}</span>
                  <span v-if="room.note" />
                  <span v-if="room.note" />
                </template>
                <template v-for="(extra, index) in formData.extras" :key="`extra-${index}`">
                  <span>{{ extra.name }}</span>
                  <span>{{ extra.count }}</span>
                  <span>AUD ${{ extra.price.toFixed(2) }}</span>
                  <span v-if="extra.note" class="sml-note">- {{ extra.note }}</span>
                  <span v-if="extra.note" />
                  <span v-if="extra.note" />
                </template>
                <template v-if="getTableCount() < 14">
                  <template v-for="i in (15 - getTableCount())" :key="i">
                    <span class="empty" />
                    <span class="empty" />
                    <span class="empty" />
                  </template>
                </template>
                <h4>Total</h4>
                <span />
                <h4>AUD ${{ summary.price.toFixed(2) }}</h4>
              </div>
            </div>
          </div>
          <div class="buttons">
            <Button class="p-button-info" label="Back" @click="back" />
            <Button class="p-button-success" label="Confirm" @click="confirm" />
          </div>
        </section>
        <section v-show="step === 5" id="section-processing">
          <h2>Processing</h2>
          <span>The system is currently processing your booking</span>
          <span>Please do not close this window</span>
          <ProgressSpinner style="width: 75px; height: 75px; margin-top: 0.5rem" strokeWidth="4"
            animationDuration="2.5s" />
        </section>
        <section v-show="step >= 6" id="section-confirmation">
          <div v-if="processingResult.error" class="confirmation-error">
            <h2>An Error Occurred</h2>
            <span>An error occurred whilst trying to process your booking</span>
            <span>Please wait a moment and try again</span>
            <Button label="Retry" icon="pi pi-refresh" @click="retry" />
          </div>
          <div v-else class="confirmation-complete">
            <div class="header">
              <h3>Your Booking is Complete</h3>
            </div>
            <span>Thank you for booking with us, you should receive a confirmation email soon.</span>
            <span>Your booking ID is: {{ processingResult.result.bookingID }}</span>
            <span>Upon arrival, please call us for the latest gate code</span>
            <span>After entering through the gate, please check if the gate closes, if not please let us know</span>
            <span>Afterwards, make your way down to reception to check in</span>
          </div>
          <!-- TODO -->
        </section>
      </form>
      <div v-show="step <= 3" class="summary">
        <input class="summary-toggle" type="checkbox" />
        <div class="brief">
          <div class="brief-info">
            <i class="pi pi-chevron-up" />
            <div class="booking-summary">
              <span class="price">AUD ${{ summary.price.toFixed(2) }}</span>
              <span class="room-summary">1 Room, 2 Guests</span>
            </div>
          </div>
          <div class="buttons">
            <Button v-if="step > 1" label="Back" @click="summaryBack" />
            <Button class="p-button-success" :label="step < 3 ? 'Continue' : 'Book'"
              :disabled="step === 0 && summary.accommodation === 0" @click="summarySubmit" />
          </div>
        </div>
        <div class="full">
          <i class="pi pi-chevron-down" />
          <h1>Booking Summary</h1>
          <div class="checkinout">
            <div class="check">
              <h4>Check-In</h4>
              <span>After 2PM</span>
            </div>
            <div class="check">
              <h4>Check-Out</h4>
              <span>Before 11AM</span>
            </div>
          </div>
          <Divider />
          <div class="dates">
            <span>{{ summary.start }}</span>
            -
            <span>{{ summary.end }}</span>
          </div>
          <div class="amount">
            <span>{{ summary.guests }} {{ (summary.guests > 1 || summary.guests === 0) ? 'Guests' : 'Guest' }}</span>
            <span>{{ summary.accommodation }} {{ (summary.accommodation > 1 || summary.accommodation === 0) ?
    'Rooms/Campsites' : 'Room/Campsite' }}</span>
            <span>{{ summary.nights }} {{ summary.nights > 1 ? 'Nights' : 'Night' }}</span>
          </div>
          <Divider />
          <div v-if="step > 0 && summary.accommodation > 0 && formData.accommodations?.length" class="accommodation">
            <div v-for="(room, index) in formData.accommodations" :key="`room-${index}`" class="accommodation-item"
              :class="{ hidden: room.type === undefined }">
              <Divider v-if="index > 0" />
              <div class="info">
                <h4>{{ getRoomName(room.type) }}</h4>
                <i v-if="step === 0" class="pi pi-trash" @click="removeAccommodation(index)" />
              </div>
              <div class="info">
                <span>{{ room.guests.adults + room.guests.children }} {{ (room.guests.adults + room.guests.children) > 1
    ?
    'Guests' : 'Guest' }}, {{ summary.nights }} {{ summary.nights > 1 ? 'Nights' : 'Night' }}</span>
                <span class="price">AUD ${{ room.price.toFixed(2) }}</span>
              </div>
            </div>
          </div>
          <div v-else-if="summary.accommodation > 0 && accommodation.length" class="accommodation">
            <div v-for="(room, index) in accommodation" :key="`room-${index}`" class="accommodation-item"
              :class="{ hidden: room.type === undefined, invalid: room.invalidate }">
              <Divider v-if="index > 0" />
              <div class="info">
                <h4>{{ getRoomName(room.type) }}</h4>
                <i class="pi pi-trash" @click="removeAccommodation(index)" />
              </div>
              <div class="info">
                <span>{{ room.guests.adults + room.guests.children }} {{ (room.guests.adults + room.guests.children) > 1
    ?
    'Guests' : 'Guest' }}, {{ summary.nights }} {{ summary.nights > 1 ? 'Nights' : 'Night' }}</span>
                <span class="price">AUD ${{ room.price.toFixed(2) }}</span>
              </div>
            </div>
          </div>
          <span v-else class="warning">Select at least one accommodation</span>
          <Divider v-if="summary.extras" />
          <div v-if="step > 1 && formData.extras?.length" class="extras">
            <h4>Extras</h4>
            <div v-for="(extra, index) in formData.extras" :key="`extra-${index}`" class="extra">
              <div class="info">
                <span class="name">{{ extra.name }}</span>
              </div>
              <div class="info">
                <span class="count">Count: {{ extra.count }}</span>
                <span class="price">AUD ${{ extra.price.toFixed(2) }}</span>
              </div>
            </div>
          </div>
          <div v-else-if="extras.length" class="extras">
            <h4>Extras</h4>
            <div v-for="(extra, index) in extras" :key="`extra-${index}`" class="extra">
              <div class="info">
                <span class="name">{{ extra.name }}</span>
                <i class="pi pi-trash" @click="removeExtra(index)" />
              </div>
              <div class="info">
                <span class="count">Count: {{ extra.count }}</span>
                <span class="price">AUD ${{ extra.price.toFixed(2) }}</span>
              </div>
            </div>
          </div>
          <Divider />
          <div class="price">
            <span>Total:</span>
            <span>AUD ${{ summary.price.toFixed(2) }}</span>
          </div>
          <Divider v-if="step < 2" />
          <div class="buttons">
            <Button v-if="step > 1" label="Back" @click="summaryBack" />
            <Button class="p-button-success" :label="step < 3 ? 'Continue' : 'Book'"
              :disabled="(step === 0 && (summary.accommodation === 0 || available.invalid || accommodation.filter(item => item.invalidate).length)) || loadingError"
              @click="summarySubmit" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, watch, onMounted, onBeforeMount } from 'vue';
import { storeToRefs } from 'pinia';
import * as yup from 'yup';
import * as datefns from 'date-fns';
import { useReCaptcha } from 'vue-recaptcha-v3';
import { useForm, useField } from 'vee-validate';
import { iso31661, iso31661Reserved, iso31662 } from 'iso-3166';
import { useAppStore } from '@/stores/app';
import fetchPlus from '@/utilities/FetchPlus';
import getHost from '@/utilities/GetHost';
import AccommodationSelect from '@/components/booking/AccommodationSelect.vue';
import DatePicker from '@/components/booking/DatePicker.vue';
import GuestsPicker from '@/components/booking/GuestsPicker.vue';

const { executeRecaptcha, recaptchaLoaded } = useReCaptcha();

const appStore = useAppStore();
const { theme } = storeToRefs(appStore);

const EMAIL_REGX = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const prefixes = ['Mr. and Mrs.', 'Mr.', 'Mrs.', 'Ms.', 'Dr.', 'Pr.', 'Prof.'];

const countries = iso31661.map((item) => {
  return { name: item.name, code: item.alpha2, states: [] };
});

iso31661Reserved.forEach((item) => {
  if (countries.findIndex((country) => country.name === item.name) === -1) {
    countries.push({ name: item.name, code: item.alpha2, states: [] });
  }
});

iso31662.sort((a, b) => a.name.localeCompare(b.name)).forEach((item) => {
  const ndx = countries.findIndex((country) => country.code === item.parent);
  if (ndx !== -1) {
    countries[ndx].states.push({ name: item.name, code: item.code.split('-')[1] });
  }
});

const sortedCountries = countries.sort((a, b) => {
  if (a.name === 'Australia') {
    return -1;
  } else if (b.name === 'Australia') {
    return 1;
  }
  return a.name.localeCompare(b.name);
});
const countryCodes = countries.map((item) => item.code);

const lightModeCardStyle = {
  '.input-container': {
    borderColor: 'rgba(0, 0, 0, 0.38)',
    borderRadius: '0.3em'
  },
  '.input-container.is-focus': {
    borderColor: '#3F51B5'
  },
  '.input-container.is-error': {
    borderColor: '#B00020'
  },
  '.message-text.is-error': {
    color: '#B00020'
  },
  '.message-icon.is-error': {
    color: '#B00020'
  }
};

const darkModeCardStyle = {
  '.input-container': {
    borderColor: '#9FA8DA',
    borderRadius: '0.3em',
  },
  '.input-container.is-focus': {
    borderColor: 'rgb(159, 168, 222)',
  },
  '.input-container.is-error': {
    borderColor: '#f44435',
  },
  '.message-text': {
    color: 'rgba(255, 255, 255, 0.7)',
  },
  '.message-icon': {
    color: 'rgba(255, 255, 255, 0.7)',
  },
  '.message-text.is-error': {
    color: 'rgba(244, 68, 53, 0.7)',
  },
  '.message-icon.is-error': {
    color: 'rgba(244, 68, 53, 0.7)',
  }
};

const accommodationSchema = yup.object().shape({
  accommodation: yup.array()
    .required('Required')
    .min(1, 'Required')
    .max(4, 'Invalid')
    .of(yup.object().shape({
      type: yup.object().shape({
        main: yup.string().optional(),
        sub: yup.string().optional()
      }),
      guests: yup.object().required('Required').shape({
        adults: yup.number().required('Required').min(1, 'Required').max(10, 'Too Many'),
        children: yup.number()
          .optional()
          .min(0, 'Invalid')
          .when('adults', (adults, schema) => {
            if (adults) {
              return schema.max(10 - adults, 'Too Many');
            } else {
              return schema;
            }
          })
      }),
      invalidate: yup.boolean().oneOf([false, undefined], 'Invalid')
    })),
  arrival: yup.date()
    .required('Arrival date is required')
    .typeError('Invalid date selected')
    .min(datefns.startOfTomorrow(), 'Invalid date selected'),
  departure: yup.date()
    .required('Departure date is required')
    .typeError('Invalid date selected')
    .when('arrival', (arrival, schema) => {
      if (arrival) {
        return schema
          .min(datefns.addDays(arrival[0], 1), 'Booking must be no less than 1 night')
          .max(datefns.addDays(arrival[0], 14), 'Booking cannot exceed 14 nights');
      } else {
        return schema;
      }
    })
});
const extrasSchema = yup.object().shape({
  //
});
const contactSchema = yup.object().shape({
  prefix: yup.string().optional().oneOf(prefixes, 'Invalid prefix'),
  firstname: yup.string().required('First Name is required'),
  lastname: yup.string().required('Last Name is required'),
  email: yup.string()
    .required('Email Address is required')
    .matches(EMAIL_REGX, 'Invalid email address'),
  confirmEmail: yup.string().required('Email Confirmation is required').oneOf([yup.ref('email'), null], 'Emails don\'t match'),
  phone: yup.string().required('Phone Number is required')
});
const billingSchema = yup.object().shape({
  address1: yup.string().required('Address Line 1 is required'),
  address2: yup.string().optional(),
  country: yup.string()
    .required('Country is required')
    .oneOf(countryCodes, 'Invalid country'),
  state: yup.string()
    .required('State is required')
    .when('country', (country, schema) => {
      if (country) {
        const found = countries.find((item) => item.code === country[0]);
        if (found) {
          return schema.oneOf(found.states.map((state) => state.code), 'Invalid state');
        }
      }
      return schema;
    }),
  city: yup.string().required('City is required'),
  postal: yup.string().optional()
});

let payments;
let card;

const accommodationNext = ref();
const extraNext = ref();
const extraBack = ref();
const contactNext = ref();
const contactBack = ref();
const billingBook = ref();
const billingBack = ref();

const isSecure = ref(false);
const reducedMotion = ref(false);

const honeypot = ref();
const honeypotTimer = ref(false);
const honeypotted = ref(false);

const loading = ref(true);
const loadingError = ref(false);
const processing = ref(false);
const step = ref(0);
const extrasAvailable = ref(false);
const formData = ref({});
const width = ref(document.documentElement.clientWidth);

const processingResult = reactive({
  error: true,
  result: {}
});

const available = reactive({
  invalid: false,
  standard: {
    twin: 0, // Sleeps 2
    family: 0 // Sleeps 6
  },
  deluxe: {
    king: 0, // Sleeps 2
    twin: 0, // Sleeps 2
    triple: 0, // Sleeps 3
    family: 0 // Sleeps 4
  },
  bunkroom: 0, // Sleeps 4,
  poweredcamping: 0
});

const accommodationBrief = computed(() => {
  let output = '';
  if (width.value > 530) {
    if (step.value > 0) {
      output = datefns.format(formData.value.arrival, 'PPPP');
      output += ' - ';
      output += datefns.format(formData.value.departure, 'PPPP');
    } else {
      output = datefns.format(arrival.value, 'PPPP');
      output += ' - ';
      output += datefns.format(departure.value, 'PPPP');
    }
  } else if (width.value > 405) {
    if (step.value > 0) {
      output = datefns.format(formData.value.arrival, 'PPP');
      output += ' - ';
      output += datefns.format(formData.value.departure, 'PPP');
    } else {
      output = datefns.format(arrival.value, 'PPP');
      output += ' - ';
      output += datefns.format(departure.value, 'PPP');
    }
  } else if (width.value > 277) {
    if (step.value > 0) {
      output = datefns.format(formData.value.arrival, 'PP');
      output += ' - ';
      output += datefns.format(formData.value.departure, 'PP');
    } else {
      output = datefns.format(arrival.value, 'PP');
      output += ' - ';
      output += datefns.format(departure.value, 'PP');
    }
  } else if (width.value <= 277) {
    if (step.value > 0) {
      output = datefns.format(formData.value.arrival, 'P');
      output += ' - ';
      output += datefns.format(formData.value.departure, 'P');
    } else {
      output = datefns.format(arrival.value, 'P');
      output += ' - ';
      output += datefns.format(departure.value, 'P');
    }
  }
  return output;
});

const sortedStates = computed(() => {
  const found = countries.find((_country) => _country.code === country.value);
  if (found && found.states.length) {
    return found.states.sort((a, b) => a.name.localeCompare(b.name));
  } else {
    return [];
  }
});

const summary = computed(() => {
  const nights = clamp(datefns.differenceInCalendarDays(formData.value.departure ?? departure.value, formData.value.arrival ?? arrival.value), 1);
  let guestCount = 0;
  let accommodationCount = 0;
  let extrasCount = 0;
  let computedPrice = 0.00;
  let start = '';
  let end = '';

  if (step.value > 0) {
    start = datefns.format(formData.value.arrival, 'PP');
    end = datefns.format(formData.value.departure, 'PP');

    if (width.value > 1500) {
      start = datefns.format(formData.value.arrival, 'PPP');
      end = datefns.format(formData.value.departure, 'PPP');
    } else if (width.value >= 540) {
      start = datefns.format(formData.value.arrival, 'PPPP');
      end = datefns.format(formData.value.departure, 'PPPP');
    } else if (width.value >= 420) {
      start = datefns.format(formData.value.arrival, 'PPP');
      end = datefns.format(formData.value.departure, 'PPP');
    }
  } else {
    start = datefns.format(arrival.value, 'PP');
    end = datefns.format(departure.value, 'PP');

    if (width.value > 1500) {
      start = datefns.format(arrival.value, 'PPP');
      end = datefns.format(departure.value, 'PPP');
    } else if (width.value >= 540) {
      start = datefns.format(arrival.value, 'PPPP');
      end = datefns.format(departure.value, 'PPPP');
    } else if (width.value >= 420) {
      start = datefns.format(arrival.value, 'PPP');
      end = datefns.format(departure.value, 'PPP');
    }
  }

  if (step.value > 0 && formData.value.accommodations) {
    formData.value.accommodations.forEach((value) => {
      if (value.type) {
        const numGuests = value.guests.adults + value.guests.children;
        accommodationCount++;
        guestCount += numGuests;
        computedPrice += value.price;
      }
    });
  } else {
    accommodation.value.forEach((value) => {
      if (value.type) {
        const numGuests = value.guests.adults + value.guests.children;
        accommodationCount++;
        guestCount += numGuests;

        if (value.type.main === 'standard') {
          if (value.type.sub === 'twin') {
            value.price = (140.00 * nights);
          } else {
            value.price = (260.00 * nights);
          }
        } else if (value.type.main === 'deluxe') {
          if (value.type.sub === 'king') {
            value.price = (160.00 * nights);
          } else if (value.type.sub === 'twin') {
            value.price = (160.00 * nights);
          } else if (value.type.sub === 'triple') {
            value.price = (190.00 * nights);
          } else {
            value.price = (220.00 * nights);
          }
        } else if (value.type.main === 'bunkroom') {
          value.price = (110.00 * nights);
        } else if (value.type.main === 'poweredcamping') {
          value.price = (60.00 + (numGuests - 2) * 20.00) * nights;
        } else {
          value.price = (25.00 * numGuests * nights);
        }
        computedPrice += value.price;
      }
    });
  }

  if (step.value > 1 && formData.value.extras) {
    formData.value.extras.forEach((value) => {
      extrasCount++;
      computedPrice += value.price;
    });
  } else {
    extras.value.forEach((value) => {
      extrasCount++;
      value.count = value.rooms.length;
      value.price = value.count * nights;
      computedPrice += value.price;
    });
  }

  return {
    guests: guestCount,
    accommodation: accommodationCount,
    extras: extrasCount,
    nights,
    price: computedPrice,
    start,
    end
  };
});

const { handleSubmit: accommodationSubmit } = useForm({
  validationSchema: accommodationSchema,
  initialValues: {
    accommodation: [{ type: undefined, price: 0, guests: { adults: 1, children: 0 }, isPrevious: false }],
    arrival: datefns.startOfTomorrow(),
    departure: datefns.addDays(datefns.startOfTomorrow(), 1)
  }
});

const { value: accommodation, errorMessage: accommodationError } = useField('accommodation');
const { value: arrival, errorMessage: arrivalError } = useField('arrival');
const { value: departure, errorMessage: departureError } = useField('departure');

const { handleSubmit: extraSubmit } = useForm({
  validationSchema: extrasSchema,
  initialValues: {
    extras: []
  }
});

const { value: extras, errorMessage: extrasError } = useField('extras');

const { handleSubmit: contactSubmit } = useForm({
  validationSchema: contactSchema
});

const { value: prefix, errorMessage: prefixError } = useField('prefix');
const { value: firstname, errorMessage: firstnameError } = useField('firstname');
const { value: lastname, errorMessage: lastnameError } = useField('lastname');
const { value: email, errorMessage: emailError } = useField('email');
const { value: confirmEmail, errorMessage: confirmEmailError } = useField('confirmEmail');
const { value: phone, errorMessage: phoneError } = useField('phone');

const { handleSubmit: billingSubmit } = useForm({
  validationSchema: billingSchema
});

const cardError = ref();
const { value: address1, errorMessage: address1Error } = useField('address1');
const { value: address2, errorMessage: address2Error } = useField('address2');
const { value: country, errorMessage: countryError } = useField('country');
const { value: state, errorMessage: stateError } = useField('state');
const { value: city, errorMessage: cityError } = useField('city');
const { value: postal, errorMessage: postalError } = useField('postal');

function resize(size) {
  width.value = size.width;
}

function getRoomName(type) {
  if (type) {
    if (type.main === 'standard') {
      let output = 'Standard Ensuite Room';
      if (type.sub === 'twin') {
        output += ' (Twin)';
      } else {
        output += ' (Family)';
      }
      return output;
    } else if (type.main === 'deluxe') {
      let output = 'Deluxe Garden View Room';
      if (type.sub === 'king') {
        output += ' (King)';
      } else if (type.sub === 'twin') {
        output += ' (Twin)';
      } else if (type.sub === 'triple') {
        output += ' (Triple)';
      } else {
        output += ' (Family)';
      }
      return output;
    } else if (type.main === 'bunkroom') {
      return 'Bunkroom';
    } else if (type.main === 'poweredcamping') {
      return 'Powered Camping Site';
    } else {
      return 'Unpowered Camping Site';
    }
  } else {
    return '';
  }
}

function summarySubmit() {
  if (step.value === 0) {
    accommodationNext.value.$el.click();
  } else if (step.value === 1) {
    extraNext.value.$el.click();
  } else if (step.value === 2) {
    contactNext.value.$el.click();
  } else {
    billingBook.value.$el.click();
  }
}

function summaryBack() {
  if (step.value === 1) {
    extraBack.value.$el.click();
  } else if (step.value === 2) {
    contactBack.value.$el.click();
  } else if (step.value === 3) {
    billingBack.value.$el.click();
  }
}

function removeAccommodation(index) {
  if (step.value === 0) {
    accommodation.value.splice(index, 1);

    if (accommodation.value.length === 0) {
      accommodation.value.push({ type: undefined, price: 0, guests: { adults: 1, children: 0 }, isPrevious: false });
    }
  }
}

function removeExtra(index) {
  if (step.value < 2) {
    extras.value.splice(index, 1);
  }
}

async function recaptcha() {
  await recaptchaLoaded();

  const token = await executeRecaptcha('book');

  return token;
}

async function initPaymentForm() {
  if (!window.Square) {
    throw new Error('Square.js failed to load properly');
  }
  try {
    card = await payments.card();
    await card.attach('#card-container');

    if (theme.value === 'dark') {
      await card.configure({ style: darkModeCardStyle });
    } else {
      await card.configure({ style: lightModeCardStyle });
    }
  } catch (e) {
    console.error('Initializing card failed', e);
  }
}

async function tokenize(paymentMethod) {
  const tokenResult = await paymentMethod.tokenize();
  if (tokenResult.status === 'OK') {
    cardError.value = '';
    return tokenResult.token;
  }

  cardError.value = 'Card Tokenization Failed';
  let errorMessage = `Tokenization failed with status ${tokenResult.status}`;
  if (tokenResult.errors) {
    errorMessage += ` and errors: ${JSON.stringify(tokenResult.errors)}`;
  }
  throw new Error(errorMessage);
}

async function verifyBuyer(token, values) {
  const addressLines = [values.address1];
  if (values.address2) {
    addressLines.push(values.address2);
  }

  const verificationDetails = {
    amount: summary.value.price.toFixed(2),
    billingContact: {
      addressLines,
      familyName: values.lastname,
      givenName: values.firstname,
      email: values.email,
      countryCode: values.country,
      phone: values.phone,
      state: values.state,
      city: values.city
    },
    currencyCode: 'AUD',
    intent: 'CHARGE'
  };

  if (!values.state) {
    delete verificationDetails.billingContact.state;
  }

  try {
    const results = await payments.verifyBuyer(token, verificationDetails);
    cardError.value = '';
    return results.token;
  } catch (error) {
    cardError.value = 'Buyer Verification Failed';
    const errorMessage = `Verification failed with error: ${error.message}`;
    processing.value = false;
    throw new Error(errorMessage);
  }
}

function back() {
  if (step.value === 4) {
    processing.value = false;
  }

  if (step.value > 0) {
    if (step.value === 2 && !extrasAvailable.value) {
      step.value = 0;
    } else {
      step.value--;
    }

    document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
  }
}

function confirm() {
  // TODO: validate payment content
  step.value++;

  if (honeypotted.value) {
    setTimeout(() => {
      processingResult.error = true;
      step.value++;
    }, Math.floor(Math.random() * 15000));
  } else {
    setTimeout(() => {
      alert(JSON.stringify(formData.value, null, 2));
      processingResult.error = false;
      processingResult.result = { bookingID: '660935dd31c8a2a778f1e832' };
      step.value++;
    }, Math.floor(Math.random() * (7500 - 801) - 800));

    // fetch(`${getHost()}/bookings/book`, {  // TODO: Uncomment after demonstration
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json',
    //   },
    //   body: JSON.stringify(formData.value)
    // }).then((res) => res.json()).then((result) => {
    //   processingResult.error = result.error;
    //   processingResult.result = result.result;
    //   step.value++;

    //   // eslint-disable-next-line no-alert
    //   alert(JSON.stringify(result));
    // });
  }
}

function retry() {
  window.location.reload();
}

const onAccommodationSubmit = accommodationSubmit((values) => {
  if (step.value === 0) {
    for (const [key, value] of Object.entries(values)) {
      if (key === 'accommodation') {
        formData.value.accommodations = value.filter((item) => item.type && item.type !== 'NEW');
      } else {
        formData.value[key] = value;
      }
    }

    for (let i = 0; i < formData.value.accommodations.length; i++) {
      if (formData.value.accommodations[i].type.main === 'bunkroom') {
        extrasAvailable.value = true;
      }
    }

    if (extrasAvailable.value) {
      step.value++;
    } else {
      step.value += 2;
    }

    document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
  }
});

const onPackageSubmit = extraSubmit((values) => {
  if (step.value === 1) {
    step.value++;

    for (const [key, value] of Object.entries(values)) {
      formData.value[key] = value;
    }

    document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
  }
});

const onPersonalSubmit = contactSubmit((values) => {
  if (step.value === 2) {
    step.value++;

    for (const [key, value] of Object.entries(values)) {
      formData.value[key] = value;
    }

    document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
  }
});

const onBillingSubmit = billingSubmit((values) => {
  if (step.value === 3) {
    processing.value = true;

    recaptcha().then((token) => {
      if (!honeypotTimer.value) {
        // Likely a bot, filled form too quickly, directly show success after random timeout without processing data
        honeypotted.value = true;
        alert('Too quick');
      } else if (honeypot.value) {
        // Likely a bot, honeypot was filled in, directly show success after random timeout without processing data
        honeypotted.value = true;
        alert('Honeypot filled in');
      }

      values.recaptcha = token;

      if (isSecure.value) {
        tokenize(card)
          .then((paymentToken) => {
            verifyBuyer(paymentToken, values).then((verificationToken) => {
              step.value++;
              values.payment = paymentToken;
              values.verification = verificationToken;
              formData.value = { ...formData.value, ...values };
              document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
              // alert(JSON.stringify(formData.value));
            });
          })
          .catch(() => {
            processing.value = false;
            document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
          });
      } else {
        step.value++;
        formData.value = { ...formData.value, ...values };
        document.getElementsByClassName('accommodation-scroll')[0].scrollTo({ top: 0, behavior: reducedMotion.value ? 'auto' : 'smooth' });
        // alert(JSON.stringify(formData.value));
      }
    });
  }
});

function checkAvailability() {
  if (step.value === 0 && !loading.value) {
    if (datefns.compareAsc(arrival.value, new Date()) === -1) {
      available.invalid = true;
      loadingError.value = false;
    } else if (datefns.compareAsc(arrival.value, departure.value) === -1) {
      setTimeout(() => {
        available.standard.family = Math.random() > 0.6;
        available.standard.twin = Math.floor(Math.random() * 8);
        available.deluxe.family = Math.random() > 0.6;
        available.deluxe.king = Math.floor(Math.random() * 3);
        available.deluxe.twin = Math.floor(Math.random() * 3);
        available.deluxe.triple = Math.floor(Math.random() * 2);
        available.bunkroom = Math.floor(Math.random() * 13);
        available.poweredcamping = Math.floor(Math.random() * 23);
        available.invalid = Math.random() > 0.8;
        loadingError.value = false; 

        if (accommodation.value.length) {
          const count = {
            standard: {
              family: 0,
              twin: 0
            },
            deluxe: {
              family: 0,
              king: 0,
              twin: 0,
              triple: 0
            },
            bunkroom: 0,
            poweredcamping: 0
          };

          for (let i = 0; i < accommodation.value.length; i++) {
            const item = accommodation.value[i];
            if (item.type !== undefined) {
              if (item.subtype) {
                count[item.type][item.subtype] += 1;

                if (count[item.type][item.subtype] > available[item.type][item.subtype]) {
                  item.invalidate = true;
                } else if (item.invalidate) {
                  delete item.invalidate;
                }
              } else {
                count[item.type] += 1;

                if (count[item.type] > available[item.type]) {
                  item.invalidate = true;
                } else if (item.invalidate) {
                  delete item.invalidate;
                }
              }
            }
          }
        }
      }, Math.floor(Math.random() * (7500 - 801) + 800));

