}

function getTableCount() {
  let output = 0;

  if (formData.value?.accommodations) {
    output += formData.value.accommodations.length;
    output += formData.value.accommodations.reduce((n, { note }) => n + note !== undefined, 0);
  }

  if (formData.value?.extras) {
    output += formData.value.extras.length;
    output += formData.value.extras.reduce((n, { note }) => n + note !== undefined, 0);
  }

  return output;
}

// watch(() => arrival.value, (newArrival, oldArrival) => {
//   if (newArrival !== oldArrival) {
//     checkAvailability();
//   }
// });

watch(() => departure.value, (newDeparture, oldDeparture) => {
  if (newDeparture !== oldDeparture) {
    checkAvailability();
  }
});

function clamp(value, min, max) {
  if (max) {
    return Math.min(Math.max(value, min), max);
  } else {
    return Math.max(value, min);
  }
}

onBeforeMount(() => {
  isSecure.value = window.location.protocol === 'https:' || ['localhost', '127.0.0.1'].includes(window.location.hostname);

  if (isSecure.value) {
    payments = window.Square.payments(process.env.VUE_APP_SQUARE_APP_ID, process.env.VUE_APP_SQUARE_LOCATION_ID);
  } else {
    cardError.value = 'Insecure Connection';
  }
});

onMounted(() => {
  reducedMotion.value = window.matchMedia('(prefers-reduced-motion: reduce)').matches;

  setTimeout(() => {
    honeypotTimer.value = true;
    document.querySelector('#terms').tabIndex = -1;
    document.querySelector('#terms').autocomplete = 'off';
  }, 5 * 1000);

  if (isSecure.value) {
    initPaymentForm().then(() => {
      loading.value = false;
      checkAvailability();
    });
  } else {
    loading.value = false;
    checkAvailability();
  }
});
